@import "../../css-config/mixins.scss";

.main-header{
    width: 100%;
    padding: 0 20px;
    background-color: #1e1a2d;
    color: white;
    border-bottom: 1px solid rgba(0, 0, 0, 0.274);
    z-index: 99;
    
    @include md{
        padding: 0 40px;
    
    }
    .header-context{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        max-width: 1200px;
        margin: 0 auto;
        @include md{
            height: 80px;
        }
        .left{
            list-style-type: none;
            display: none;
            gap: 25px;
            @include md{
                display: flex;
            }
            li{
                font-size: 14px;
                font-weight: 600;
                text-transform: uppercase;
                cursor: pointer;
            }
        }
        .center{
            display: flex;
            align-items: center;
            font-size: 1px;
            font-weight: 700;
            cursor: pointer;
            @include md{
                font-size: 34px;
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
            }
            h1{
                font-size: 20px;
            }
            span{
                
                font-size: 20px;
                color: rgb(54, 183, 235);
            }
            img{
                height: 65px;
                width: 100px;
            }
        }
        .right{
            display: flex;
            align-items: center;
            gap: 20px;
            @include md{
                gap: 25px;
            }
            svg{
                font-size: 20px;
                cursor: pointer;
                @include md{
                    font-size: 24px;
                }
            }
            .cart-icon{
                position: relative;
                span{
                    min-width: 20px;
                    text-align: center;
                    color: black;
                    background-color: white;
                    padding: 2.5px;
                    position: absolute;
                    top: -5px;
                    right: -12px;
                    font-size: 12px;
                    line-height: 1;
                    border-radius: 10px;

                }
            }
        }
    }

    &.sticky-header{
        position: sticky;
        top: 0;
        transform: translateY(-80%);
        animation: stickyHeader 0.3s ease forwards; 
    }
}

@keyframes stickyHeader {
    0% {transform: translateY(-80%);}
    100% {transform: translateY(0);}
    
}